<template>
  <div class="hidden-print option-btn">
    <section @click="print">
      <i class="fa fa-print"></i>
      <span>打印</span>
    </section>
    <section @click="up">
      <i class="fa fa-eye"></i>
      <span>预览,并保存为在线简历</span>
    </section>
  </div>
</template>

<script>
  import $ from 'jquery'
  export default{
      name:'optionBtn',
      data(){
          return{

          }
      },
      methods:{
          print(){
              window.print();
          },
        up(){
          //获取名字
          var $name=$('#name');
          var $input=$name.find('input');
          var $h1=$name.find('h1');

          var username='';
          ($input.length!=0)&&(username=$input[0].value);
          ($h1.length!=0)&&(username=$h1.html());


          //修改body
          var body_=$('<div>'+$('#app').html()+'</div>');
          body_.find('.option-btn').remove();
          var head=['<head>',$(document.head).html(),'</head>'];

          var body=['<body><div id="app">',body_.html(),'</div></body>'];


          var html=['<!DOCTYPE html><html>','','</html>'];

          html[1]=head.join('')+body.join('');

          //全局替换+号
          var htmlStr=html.join('').replace(/\+/g,'/plus/');

          var data={
            html:htmlStr
          }

           $.ajax({
             method:'post',
             url:'/save/'+username,
             data:'data='+JSON.stringify(data),
             success:function(result){
               window.open(result);
             }
           })
        }

      }
  }
</script>

<style>
  .option-btn{
    width: 100%;
    /*background-color: #00b3ee;*/
    text-align: center;
    padding: 1rem 0 3rem 0 ;
  }
  .option-btn section{
    display: inline-block;
    width: 6rem;
    font-size: 2rem;
    margin: 0 0 0 5rem;
    transition: all 0.2s linear;
  }
  .option-btn section:nth-child(2){
    width: 20rem;
  }
  .option-btn section i{
    text-align: center;
    line-height: 2rem;
    width: 2rem;
    height: 2rem;
    transition: all 0.2s linear;
  }
  .option-btn section:hover{
    color: red;
    cursor: pointer;
    transform: scale(1.3);
  }
  .option-btn section:hover i{
    transform: rotate(360deg);
  }

</style>



