<template>
    <div :class="className" class="base-info">
        <ul>
            <li class="info-item" v-for="(item,index) in info" ref="info">
                <i class="fa fa-minus-circle" @click="deleteItem(index)" style="display: none"></i>
                <i class="fa icon" :class="item.icon" @click="selectIcon(index)"></i>
                <div class="message">
                    <p v-show="!edit">{{item.message}}</p>
                    <input type="text" maxlength="23" title="" v-model=item.message v-show="edit"
                           @focus="editItem($event)" @blur="blurItem($event)">
                </div>
            </li>
        </ul>
        <div class="editBtn" v-show="over">
            <i class="btnPlus fa fa-plus-square" @click="plusItem" title="添加一项"></i>
        </div>
        <icon-block v-if="mask" @changeIcon="changeIcon"></icon-block>
        <div class="mask" @click="hideMask" v-show="mask"></div>
    </div>
</template>

<script>
import iconBlock from './IconCollection.vue'

export default {
  name: 'baseInfo',
  props:['className'],
  components: {
    iconBlock
  },
  data () {
    return {
      info: [
        {icon: 'fa-user',message: '21岁'},
        {icon: 'fa-map-marker',message: '杭州市'},
        {icon: 'fa-phone-square',message: '159000000000'},
        {icon: 'fa-envelope-open',message: 'tencent@qq.com'},
        {icon: 'fa-github-square',message: 'github'}
        ],
      over: false,
      mask: false,
      edit: false
    }
  },
  mounted() {
    this.bindListener();
  },
  updated() {
    this.bindListener();
  },
  methods: {
    mouseOver(event) {
      let el = event.currentTarget;
//      console.log(el.tagName);
      el.style.outline = '1px dashed #00aa88';
      el.style.background = 'rgba(0,0,0,.3)';
      this.over = true;
      this.edit = true;
      if (el.tagName === 'LI') {
        el.firstChild.style.display = 'block'
      }
    },
    mouseOut(event) {
      let el = event.currentTarget;
      el.style.outline = '1px dashed transparent';
      el.style.background = '';
      this.over = false;
      this.edit = false;
      if (el.tagName === 'LI') {
        el.firstChild.style.display = 'none'
      }
    },
    bindListener() {
      let that = this;
      this.$el.addEventListener('mouseover', this.mouseOver);
      this.$el.addEventListener('mouseout', this.mouseOut);
      this.$refs.info.forEach(function (el) {
        el.addEventListener('mouseover', that.mouseOver);
        el.addEventListener('mouseout', that.mouseOut);
      });
    },
    plusItem() {
      this.info.push({icon: 'fa-star',message: 'xxxxxx'});
    },
    deleteItem(index) {
      this.info.splice(index,1);
    },
    editItem($event) {
      let el = $event.currentTarget;
//      el.style.border = '1px solid #beccff';
//      el.style.boxShadow = '0 0 4px #beccff'
    },
    blurItem($event) {
      let el = $event.currentTarget;
//      el.style.border = '1px solid transparent';
//      el.style.boxShadow = 'none'
    },
    selectIcon(index) {
      this.index = index;
      this.mask = true;
      document.body.style.overflow = 'hidden';
    },
    hideMask() {
      this.mask = false;
      document.body.style.overflow = 'auto';
    },
    changeIcon: function (val) {
      this.$set(this.info[this.index],'icon',val);
      this.hideMask();
    }
  }
}
</script>

<style>
    .base-info {
        position: relative;
    }
    .info-item {
        position: relative;
        padding-left: 1.67rem;
        display: flex!important;
        text-align: center;
        vertical-align: middle;
    }
    .icon {
        flex: 0 0 2rem;
        font-size: 2rem!important;
        vertical-align: middle;
        line-height: 2.8rem!important;
    }
    .message {
        flex: 1;
        border: 1px solid transparent;
        font-size: 1.4rem;
      line-height: 2.5rem;
        height: 2.5rem;
      font-weight: 600;
    }
    .message p {
        display: inline-block;
        width: 100%;
        text-align: start;
        padding: 1px 0 0 0.5rem;
        margin: 0;
    }
    .message input {
        width: 100%;
        background: none;
        border: none;
        padding-left: 0.5rem;
    }
    input:focus{
      outline: none;
      border: none;
    }
    #leftPane .fa-plus-square{
      transition:All 0.4s ease-in-out;
    }
    #leftPane .fa-plus-square:hover {
      cursor: pointer;
        transform: rotate(360deg);
        color: #4F5D8E;
    }
    .base-info .fa:hover {
        cursor: pointer;
    }

</style>



