<template>
    <div :class="[className,{hoverBlock:hover}]" @mouseover="isHover" @mouseleave="isNotHover">
        <header class="clear-fix">
            <span class="fa fa-graduation-cap"></span>
            <span>
                <h2>教育背景</h2>
            </span>
        </header>

        <ul class="intro-content-1">
          <content-o-r-input
            v-for="(content,index) in contents"
            :model="'contentModelOne'"
            :tittle1="content.tittle1"
            :tittle2="content.tittle2"
            :tittle3="content.tittle3"
            :content="content.content"
            :index="index"
            :key="index"
            :change="changeData(index)"
            :edit="edit(index)"
            :edited="edited(index)"
            :del="del(index)"
            :inputIndex="inputIndex"
            />

        </ul>

      <section class="menuBtn" v-show="hover">
        <a title="删除此版块" @click="del_">
          <section class="fa fa-minus-circle"></section>
        </a>
        <a title="增加一条" @click="add">
          <section class="fa fa-plus-circle"></section>
        </a>
      </section>
    </div>
</template>

<script>
  import contentORInput from './ContentORInput/ContentORInput.vue'

    export default{
      name:'educationBlock',
      props:['className','del_'],
      components:{
        contentORInput
      },
        data(){
            return{
              contents:[
                {
                  tittle1:'XXXX-XXXX',
                  tittle2:'XXXX大学',
                  tittle3:'本科',
                  content:`<p>主修课程<br/>XXXXXX</p>`
                }
              ],
              hover:false,
              inputIndex:-1
            }
        },
      methods:{
        isHover:function(e){
          this.hover=true
        },
        isNotHover:function(e){
          this.hover=false
        },
        changeData:function(index){
          var self=this;
          return function(key,value){
            self.contents[index][key]=value;
          }
        },
        edit:function(index){
          var self=this;
          return function(){
            self.inputIndex=index;
          }
        },
        edited:function(){
          var self=this;
          return function(){
            self.inputIndex=-1;
          }
        },
        del:function(index){
          var self=this;
          return function(){
            self.inputIndex=-1;
            self.contents.splice(index,1)
          }
        },
        add:function(){
            this.contents.length<5&&this.contents.push({
              tittle1:'xxxx-xxxx',
              tittle2:'xxxx',
              tittle3:'xx',
              content:`<p>xxxx</p>`

            })
        }

      }
    }
</script>

<style>

</style>



