<template>
  <div id="name" :class="[className,{hover:hover}]"  @mouseover="isHover" @mouseleave="isNotHover">
    <div v-if="isEdit">
      <head-info-input
        :name="name"
        :content="content"
        :edited="edited"
        :changeName="changeName"
        :changeContent="changeContent"
      />
    </div>
    <div v-else>
      <div :class="['content',{hover:hover}]">
        <h1>{{name}}</h1>
        <h3 class="wanted">
          {{content}}
        </h3>
      </div>
      <section class="clear-fix btn" v-show="hover" @click="edit">
        <div>编辑</div>
        <i class="fa fa-align-justify"></i>
      </section>
    </div>

  </div>

</template>

<script>
  import headInfoInput from './InputBlock/HeadInfoInput.vue'
  import $ from 'jquery'

    export default{
        name:'headerName',
        props:['className'],
      components:{
        headInfoInput
      },
        data(){
            return{
              name:'你的名字',
              content:'求职目标：XXX',
              hover:false,
              isEdit:false
            }
        },
      methods:{
        isHover:function(e){
          this.hover=true
        },
        isNotHover:function(e){
          this.hover=false
        },
        edited:function(){
            this.isEdit=false;
        },
        edit:function(){
            this.isEdit=true;
        },
        changeName:function(value){
            $('title').html(value+'的简历')
            this.name=value
        },
        changeContent:function(value){
            this.content=value
        }
      }
    }
</script>

<style>

  .head-name-block section.btn{
    position: absolute;
    right: 1rem;
    top: 0;
  }
  .head-name-block section.btn i,
  .head-name-block section.btn div{
    display: block;
    float: right;
    font-size: 2rem;
    line-height: 3rem;
    text-align: center;
    padding: 0 0.2rem;
    transition: all 0.3s linear;
  }
  .head-name-block section.btn:hover{
    cursor: pointer;
    color: red;
  }
  .head-name-block section.btn:hover i{

    transform: rotateY(180deg);
  }
  .head-name-block.hover{
    background-color: transparent;
    border: 1px dashed black;
  }
  .head-name-block .content.hover{
    background-color: #cdcdcd;
  }
</style>



