<template>
  <div class="inputBlock">
    <section>
      <input v-model="name_" @change="changeName($event.target.value)">
    </section>
    <section>
      <input v-model="content_" @change="changeContent($event.target.value)">
    </section>
    <section class="clear-fix">
      <span @click="edited">完成</span>
      <i class="fa fa-check-square checked" @click="edited"></i>
    </section>
  </div>
</template>

<script>
  export default{
    name:'headInfoInput',
    props:['name','content','edited','changeName','changeContent'],
    data(){
        return{
          name_:this.name,
          content_:this.content
        }
    },
    methods:{
      getInputWidth:function(val){
        let width=0;
        val.split('').forEach(function(char){
          if(/[\u4e00-\u9fa5]/.test(char)) width+=2;
          else width++;
        })
        return width;
      }
    },
    watch:{
      name_:function(val){
        this.name_=this.getInputWidth(val)>20?val.substr(0,val.length-1):val;
      },
      content_:function(val){
        this.content_=this.getInputWidth(val)>60?val.substr(0,val.length-1):val;
      }
    }

  }
</script>

<style>

  .head-name-block .inputBlock section input:focus,
  .head-name-block .inputBlock section textarea:focus{
    outline: none;
  }
  .head-name-block .inputBlock section:nth-child(1) input{
    width: 100%;
    font-size: 5rem;
    font-weight: 500;
    height: 6.5rem;
    line-height: 6.5rem;
    border: 1px dashed #cdcdcd;
  }
  .head-name-block .inputBlock section:nth-child(2) input{
    width: 100%;
    font-size: 2rem;
    height: 2.5rem;
    line-height: 2.5rem;
    resize: none;
    border: 1px dashed #cdcdcd;

  }
  .head-name-block .inputBlock section:nth-child(3) i.checked,
  .head-name-block .inputBlock section:nth-child(3) span{
    display: block;
    float: right;
    font-size: 2.5rem;
    margin-right: 0.5rem;
    line-height: 2.5rem;
    text-align: start;
    transition: all 0.3s linear;
  }
  .head-name-block .inputBlock section:nth-child(3) i.checked{
    margin-right: 0;
    text-align: center;
    line-height: 2.5rem;
    width: 2.5rem;
    height: 2.5rem;
  }

  .head-name-block .inputBlock section:nth-child(3) span{
    width: 0;
    white-space: nowrap;
    text-wrap: none;
    overflow: hidden;
  }
  .head-name-block .inputBlock section:nth-child(3):hover{
    cursor: pointer;
    color: red;
  }
  .head-name-block .inputBlock section:nth-child(3):hover span{
    width: 5rem;
  }
  .head-name-block .inputBlock section:nth-child(3):hover i.checked{
    transform: rotate(360deg);
  }
</style>



