<template>

  <article :class="{hover:isHover_}" class="intro-content-3" @mouseover="isHover" @mouseleave="isNotHover">
    <div v-html="content"></div>

    <section v-show="isHover_" class="del-btn" tittle="删除" @click="del">
      <i class="fa fa-minus-square" ></i>
      <div>删除</div>
    </section>
    <section v-show="isHover_" class="editor-btn" @click="edit">
      <i class="fa fa-align-justify"></i>
      <div >编辑</div>
    </section>
  </article>


</template>

<script>
  export default{
    name:'contentModelThree',
    props:['content','edit','del'],
    data(){
        return{
          showThis:false,
          hoverThis:{},
          isHover_:false
        }
    },
    methods:{
      isHover:function(e){
        this.isHover_=true;
        this.hoverThis={backgroundColor:'#cdcdcd'};
      },
      isNotHover:function(e){
        this.isHover_=false;
        this.hoverThis={};
      }
    }
  }
</script>

<style>
  .intro-content-3{
    position: relative;
  }
  .intro-content-3 .del-btn,
  .intro-content-3 .editor-btn
  {
    position: absolute;
    bottom:0;
    right: 0;
    transition: all 0.2s linear;
  }
  .intro-content-3 .del-btn{
    bottom:1.5rem;
  }

  .intro-content-3 .del-btn:hover,
  .intro-content-3 .editor-btn:hover{
    cursor: pointer;
    color: red;
  }
  .intro-content-3 .del-btn:hover i,
  .intro-content-3 .editor-btn:hover i
  {
    transform: rotate(360deg);
  }


  .intro-content-3 .editor-btn i,
  .intro-content-3 .del-btn i,
  .intro-content-3 .editor-btn div,
  .intro-content-3 .del-btn div{
    font-size: 1.5rem;
    line-height: 2rem;
    display: inline-block;
    vertical-align: middle
  }

  .intro-content-3 .editor-btn i,
  .intro-content-3 .del-btn i{
    text-align: center;
    width: 1.5rem;
    transition: all 0.2s linear;

  }


  .intro-content-3 .editor-btn div,
  .intro-content-3 .del-btn div{
    text-align: start;
    width: 3rem;
  }

</style>



