<template>

  <li :class="{hover:isHover_}" @mouseover="isHover" @mouseleave="isNotHover">
    <section class="clear-fix">
      <span>
        <p>{{tittle1}}</p>
      </span>
      <span>
        <p>{{tittle2}}</p>
      </span>
      <span>
        <p>{{tittle3}}</p>
      </span>
    </section>
    <section v-html="content"></section>
    <section v-show="isHover_" class="del-btn" tittle="删除" @click="del">
      <i class="fa fa-minus-square" ></i>
      <div>删除</div>
    </section>

    <section v-show="isHover_" class="editor-btn" @click="edit">
      <i class="fa fa-align-justify"></i>
      <div >编辑</div>
    </section>
  </li>
</template>

<script>
  export default{
    name:'contentModelOne',
    props:['tittle1','tittle2','tittle3','content','edit','del'],
    data(){
      return{
        showThis:false,
        hoverThis:{},
        isHover_:false
      }
    },
    methods:{
      isHover:function(e){
        this.isHover_=true;
        this.hoverThis={backgroundColor:'#cdcdcd'};
      },
      isNotHover:function(e){
        this.isHover_=false;
        this.hoverThis={};
        }

    }


  }
</script>

<style>
  .hover{
    background-color:#cdcdcd;
  }
  .intro-content-1 li{
    position: relative;
  }
  .intro-content-1 li{
    transition: all 0.2s linear;
  }
  .intro-content-1 .del-btn,
  .intro-content-1 .editor-btn
  {
    position: absolute;
    top:0;
    right: 0.7rem;
    transition: all 0.2s linear;
  }
  .intro-content-1 .editor-btn{
    top:1.5rem;
  }

  .intro-content-1 .del-btn:hover,
  .intro-content-1 .editor-btn:hover{
    cursor: pointer;
    color: red;
  }
  .intro-content-1 .del-btn:hover i,
  .intro-content-1 .editor-btn:hover i
  {
    transform: rotate(360deg);
  }


  .intro-content-1 .editor-btn i,
  .intro-content-1 .del-btn i,
  .intro-content-1 .editor-btn div,
  .intro-content-1 .del-btn div{
    font-size: 1.5rem;
    line-height: 2rem;
    display: inline-block;
    vertical-align: middle
  }

  .intro-content-1 .editor-btn i,
  .intro-content-1 .del-btn i{
    text-align: center;
    width: 1.5rem;
    transition: all 0.2s linear;

  }


  .intro-content-1 .editor-btn div,
  .intro-content-1 .del-btn div{
    text-align: start;
    width: 3rem;
  }


</style>



