<template>
  <div :class="[className,{hoverBlock:hover}]" @mouseover="isHover" @mouseleave="isNotHover">

  <header class="clear-fix">
            <span class="fa fa-file"></span>
            <span>
                <h2>自我评价</h2>
            </span>
        </header>

        <content-o-r-input
          :model="'contentModelThree'"
          :content="content"
          :change="changeData"
          :edit="edit"
          :edited="edited"
          :del="del"
          :inputIndex="inputIndex"
        />
    <section class="menuBtn" v-show="hover">
      <a title="删除此版块" @click="del_">
        <section class="fa fa-minus-circle"></section>
      </a>
    </section>
    </div>
</template>

<script>
  import contentORInput from './ContentORInput/ContentORInput.vue'

  export default{
    name:'selfAssessment',
    props:['className','del_'],
    components:{
      contentORInput
    },
    data(){
      return{
        content:`<p>
               谈谈自己的优点
           </p>`,
        hover:false,
        inputIndex:0

      }
    },
    methods: {
      isHover: function (e) {
        this.hover = true
      },
      isNotHover: function (e) {
        this.hover = false
      },
      changeData: function (value) {
        this.content = value;
      },
      edit: function () {
        this.inputIndex = 1;
      },
      edited: function () {
        this.inputIndex = 0;
      },
      del: function () {
        this.content = ''
      }
    }
  }
</script>

<style></style>



