<template>
    <div :class="className">
        <ul>
            <li v-for="(item,index) in skills" ref="skill" class="skill-item">
                <i class="fa fa-minus-circle" @click="deleteItem(index)" style="display: none"></i>
                <input type="text" class="skill-name" title="" v-model="item.name" @focus="editItem($event)" @blur="blurItem($event)" v-show="edit">
                <div v-show="!edit" class="skill-name-shift">{{item.name}}</div>
                <section @click="setVal($event)">
                    <progress :value="item.value" max="100"/>
                    <div class="set-val">
                        <input type="text" title="" v-model="item.value" maxlength="3">
                        <span>{{item.value | level}}</span>
                    </div>
                </section>
            </li>
        </ul>
        <div class="editBtn" v-show="over">
            <i class="btnPlus fa fa-plus-square" @click="plusItem" title="添加一项"></i>
        </div>
        <div class="mask" @click="hideMask" v-show="mask"></div>
    </div>
</template>

<script>
export default{
  name:'skillRank',
  props:['className'],
  data () {
    return {
      skills: [
        {name: '技能名', value: '70'},
        {name: '点击技能条改变技能值', value: '50'}
        ],
      over: false,
      mask: false,
      edit: false,
      el: null
    }
  },
  mounted() {
    this.bindListener();
  },
  updated() {
    this.bindListener();
  },
  methods: {
    mouseOver(event) {
      let el = event.currentTarget;
//      console.log(el.tagName);
      el.style.outline = '1px dashed #00aa88';
      el.style.background = 'rgba(0,0,0,.3)';
      this.over = true;
      this.edit = true;
      if (el.tagName === 'LI') {
        el.firstChild.style.display = 'block'
      }
    },
    mouseOut(event) {
      let el = event.currentTarget;
      el.style.outline = '1px dashed transparent';
      el.style.background = '';
      this.over = false;
      this.edit = false;
      if (el.tagName === 'LI') {
        el.firstChild.style.display = 'none'
      }
    },
    bindListener() {
      let that = this;
      this.$el.addEventListener('mouseover', this.mouseOver);
      this.$el.addEventListener('mouseout', this.mouseOut);
      this.$refs.skill.forEach(function (el) {
        el.addEventListener('mouseover', that.mouseOver);
        el.addEventListener('mouseout', that.mouseOut);
      });
    },
    plusItem() {
      this.skills.push({name: 'ruby',value: '60'});
    },
    deleteItem(index) {
      this.skills.splice(index,1);
    },
    editItem($event) {
      let el = $event.currentTarget;
//      el.style.border = '1px solid #beccff';
//      el.style.boxShadow = '0 0 4px #beccff';
    },
    blurItem($event) {
      let el = $event.currentTarget;
//      el.style.border = '1px solid transparent';
//      el.style.boxShadow = 'none'
    },
    setVal($event) {
      let el = $event.currentTarget;
      this.el = el;
      el.lastChild.style.display = 'block';
      this.mask = true;
    },
    hideMask() {
      this.el.lastChild.style.display = 'none';
      this.mask = false;
    }
  },
  filters: {
    level(val) {
      if(val<=55) {
        return '了解';
      } else if (55<val&&val<=75) {
        return '掌握';
      } else if(val>75){
        return '精通';
      }
    }
  }
}
</script>

<style>
    .skill-block {
        position: relative;
    }
    .skill-item {
        position: relative;
        padding-left: 1.67rem;
      font-weight: 600;
    }
    .skill-item section {
        width: 95%;
    }
    .skill-block ul>li progress{
        height: 1rem;
        cursor: pointer;
    }
    .skill-name {
        background: none;
        width: calc(100% - 1rem);
        border: none;
    }
    .skill-name:focus{
      border-bottom: 1px dashed #cdcdcd;
    }
    .skill-name-shift {
        padding: 0 ;
        border: 1px solid transparent;
    }
    .set-val {
        position: absolute;
        bottom: -1.8rem;
        display: none;
        z-index: 1001;
    }
    .set-val input{
      text-align: center;
        font-size: 1.5rem;
        line-height: 2rem;
        width: 4rem;
        height: 2rem;
        color: #000;
        border-radius: .5rem;
      border: 1px solid black;
    }

</style>



