<template>
  <li>
    <section>
      <textarea v-model.trim="content_" @change="changeData($event.target.value)"></textarea>
    </section>
    <section class="clear-fix">
      <span @click="edited">完成</span>
      <i class="fa fa-check-square checked" @click="edited"></i>
    </section>
  </li>
</template>

<script>
  export default{
    name:'inputContentModelTwo',
    props:['content','change','edited'],
    data(){
        return{
          content_:this.content.trim()
        }
    },
    methods:{
      changeData:function(value){
        this.change(value);
      }
    }

  }
</script>

<style>

  .intro-content-2 li section textarea{
    width: 100%;
    height: 4rem;
    font-size: 2rem;
    border: 1px dashed #cdcdcd;
    resize: none;
    background-color: transparent;
  }
  .intro-content-2 li section:nth-child(2) i.checked,
  .intro-content-2 li section:nth-child(2) span{
    display: block;
    float: right;
    font-size: 2.5rem;
    margin-right: 0.5rem;
    line-height: 2.5rem;
    text-align: start;
    transition: all 0.3s linear;
  }
  .intro-content-2 li section:nth-child(2) i.checked{
    margin-right: 0;
    text-align: center;
    line-height: 2.5rem;
    width: 2.5rem;
    height: 2.5rem;
  }

  .intro-content-2 li section:nth-child(2) span{
    width: 0;
    white-space: nowrap;
    text-wrap: none;
    overflow: hidden;
  }
  .intro-content-2 li section:nth-child(2):hover{
    cursor: pointer;
    color: red;
  }
  .intro-content-2 li section:nth-child(2):hover span{
    width: 5rem;
  }
  .intro-content-2 li section:nth-child(2):hover i.checked{
    transform: rotate(360deg);
  }
</style>



