<template>
  <li >
        <section class="clear-fix">
        <span>
          <input v-model.trim="tittle1_"  @change="changeData($event.target.value,'tittle1')" >
        </span>
          <span>
          <input v-model.trim="tittle2_" @change="changeData($event.target.value,'tittle2')">
        </span>
          <span>
          <input v-model.trim="tittle3_" @change="changeData($event.target.value,'tittle3')">
        </span>
        </section>
        <section>
          <textarea v-model.trim="content_" @change="changeContent($event.target.value)"></textarea>
        </section>
        <section class="clear-fix">
          <span @click="edited">完成</span>
          <i class="fa fa-check-square checked" @click="edited"></i>
        </section>
  </li>
</template>

<script>
  export default{
    name:'inputContentModelOne',
    props:['tittle1','tittle2','tittle3','content','change','edited'],
    data(){
      return{
        tittle1_:this.tittle1,
        tittle2_:this.tittle2,
        tittle3_:this.tittle3,
        content_:this.content.replace(/<br\/>/g,'\n').replace('<p>','').replace('</p>','').trim()
      }
    },
    methods:{
      changeData:function(value,key){
        this.change(key,value);
      },
      changeContent:function(value){
        value=value.replace(/\n/g,'<br/>');
        value=['<p>',value,'</p>'].join('');
        this.change('content',value)
      },
      getInputWidth:function(val){
        let width=0;
        val.split('').forEach(function(char){
          if(/[\u4e00-\u9fa5]/.test(char)) width+=2;
          else width++;
        })
        return width;
      },



    },
    computed:{

    }
    ,
    watch:{
      tittle1_:function(val){
        this.tittle1_=this.getInputWidth(val)>15?val.substr(0,val.length-1):val;
      },
      tittle2_:function(val){
        this.tittle2_=this.getInputWidth(val)>15?val.substr(0,val.length-1):val;
      },
      tittle3_:function(val){
        this.tittle3_=this.getInputWidth(val)>11?val.substr(0,val.length-1):val;
      },
      content_:function(val){
          this.content_=val;
      }

    },
    mounted(){
//      console.log(this.edited)
    }
  }
</script>

<style>
  .intro-content-1 li section:nth-child(1) span input{
    width: 100%;
    line-height: 2rem;
    letter-spacing: 0.2rem;
    font-size: 2rem;
    border: 1px dashed #cdcdcd;
  }
  .intro-content-1 li section:nth-child(2) textarea{
    width: 100%;
    height: 10rem;
    resize: none;
    border: 1px dashed #cdcdcd;
  }

  .intro-content-1 li section:nth-child(3) i.checked,
  .intro-content-1 li section:nth-child(3) span{
    display: block;
    float: right;
    font-size: 2.5rem;
    margin-right: 0.5rem;
    line-height: 2.5rem;
    text-align: start;
    transition: all 0.3s linear;
  }
  .intro-content-1 li section:nth-child(3) i.checked{
    margin-right: 0;
    text-align: center;
    line-height: 2.5rem;
    width: 2.5rem;
    height: 2.5rem;
  }
  .intro-content-1 li section:nth-child(3){
    overflow: hidden;
  }
  .intro-content-1 li section:nth-child(3) span{
    width: 0;
    white-space: nowrap;
    text-wrap: none;
    overflow: hidden;
  }
  .intro-content-1 li section:nth-child(3):hover{
    cursor: pointer;
    color: red;
  }
  .intro-content-1 li section:nth-child(3):hover span{
    width: 5rem;
  }
  .intro-content-1 li section:nth-child(3):hover i.checked{
    transform: rotate(360deg);
  }
</style>



