<template>
    <div :class="className" >
        <h1>兴趣爱好</h1>
        <ul>
            <li class="clear-fix hobby-item" ref="hobby" v-for="(item,index) in hobbies">
                <section class="fa hobby-icon" :class="item.icon" @click="selectIcon(index)"></section>
                <input class="hobby-name" type="text" title="" v-model="item.message" maxlength="4" v-show="edit" @focus="editItem($event)" @blur="blurItem($event)" >
                <div v-show="!edit" class="hobby-name-shift">{{item.message}}</div>
                <i class="fa fa-minus-circle" @click="deleteItem(index)" style="display: none"></i>
            </li>
        </ul>
        <div class="editBtn" v-show="over">
            <i class="btnPlus fa fa-plus-square" @click="plusItem" title="添加一项"></i>
        </div>
        <icon-block v-if="mask" @changeIcon="changeIcon"></icon-block>
        <div class="mask" @click="hideMask" v-show="mask"></div>
    </div>
</template>

<script>
import iconBlock from './IconCollection.vue'
export default{
  name:'hobbyBlock',
  props:['className'],
  components:{
    iconBlock
  },
  data(){
    return{
      hobbies: [
        {icon: 'fa-globe',message: '旅游'},
        {icon: 'fa-cutlery',message: '美食'},
        {icon: 'fa-camera',message: '拍照'},
        {icon: 'fa-headphones',message: '音乐'}
        ],
      over: false,
      select: false,
      mask: false,
      edit: false,
      index: null
    }
  },
  mounted() {
    this.bindListener();
  },
  updated() {
    this.bindListener();
  },
  watch: {
    hobbies: {
      handler: function () {
        let el = this.$el;
        el.style.outline = '1px dashed transparent';
        el.style.background = '';
      },
      deep: true
    }
  },
  methods: {
    mouseOver(event) {
      let el = event.currentTarget;
//      console.log(el.tagName);
      el.style.outline = '1px dashed #00aa88';
      el.style.background = 'rgba(0,0,0,.3)';
      this.over = true;
      this.edit = true;
      if (el.tagName === 'LI') {
        el.lastChild.style.display = 'block'
      }
    },
    mouseOut(event) {
      let el = event.currentTarget;
      el.style.outline = '1px dashed transparent';
      el.style.background = '';
      this.over = false;
      this.edit = false;
      if (el.tagName === 'LI') {
        el.lastChild.style.display = 'none'
      }
    },
    bindListener() {
      let that = this;
      this.$el.addEventListener('mouseover', this.mouseOver);
      this.$el.addEventListener('mouseout', this.mouseOut);
      this.$refs.hobby.forEach(function (el) {
        el.addEventListener('mouseover', that.mouseOver);
        el.addEventListener('mouseout', that.mouseOut);
      });
    },
    plusItem() {
      this.hobbies.push({icon: 'fa-star',message: '睡觉'});
    },
    deleteItem(index) {
      this.hobbies.splice(index,1);
    },
    editItem($event) {
      let el = $event.currentTarget;
      el.style.border = '1px solid #beccff';
      el.style.boxShadow = '0 0 4px #beccff';
    },
    blurItem($event) {
      let el = $event.currentTarget;
      el.style.border = '1px solid transparent';
      el.style.boxShadow = 'none'
    },
    selectIcon(index) {
      this.index = index;
      this.mask = true;
      document.body.style.overflow = 'hidden';
    },
    hideMask() {
      this.mask = false;
      document.body.style.overflow = 'auto';
    },
    changeIcon: function (val) {
      this.$set(this.hobbies[this.index],'icon',val);
      this.hideMask();
    }
  }
}
</script>

<style>
    .hobby-block {
        position: relative;
      overflow: visible;
    }
    .hobby-block :after{
      content:".";display:block;height:0;clear:both;visibility:hidden
    }
    .hobby-item {
        position: relative;
    }
    .hobby-item .fa {
        cursor: pointer;
    }
    .hobby-icon:hover {
        transition:All 0.4s ease-in-out;
        transform: rotate(360deg);
    }
    /*.hobby-name-shift {*/
        /*height: 42px;*/
        /*font-size: 1.4rem;*/
        /*border: 1px solid transparent;*/
    /*}*/
    .hobby-name,.hobby-name-shift {
        background: none;
        width: 100%;
        border: 1px solid transparent;
        font-size: 1.4rem;
        display: block;
        text-align: center;
        padding: 10px 0;
    }
</style>



