<template>
  <div>
    <div v-if="isContentOne">
      <div v-if="index===inputIndex">
        <input-content-model-one
          v-if="true"
          :tittle1="tittle1"
          :tittle2="tittle2"
          :tittle3="tittle3"
          :content="content"
          :index="index"
          :change="change"
          :edited="edited"
        />
      </div>
      <div v-else>
        <content-model-one
          :tittle1="tittle1"
          :tittle2="tittle2"
          :tittle3="tittle3"
          :content="content"
          :index="index"
          :edit="edit"
          :del="del"
        />
      </div>



  </div>

    <div v-if="isContentTwo">
      <div v-if="index===inputIndex">
        <input-content-model-two
          v-if="true"
          :content="content"
          :index="index"
          :change="change"
          :edited="edited"
        />
      </div>
      <div v-else>
        <content-model-two
          :content="content"
          :index="index"
          :edit="edit"
          :del="del"
        />
      </div>
    </div>
    <div v-if="isContentThree">
      <div v-if="inputIndex==1">
        <input-content-model-three
          v-if="true"
          :content="content"
          :change="change"
          :edited="edited"
        />
      </div>
      <div v-else>
        <content-model-three
          :content="content"
          :edit="edit"
          :del="del"
        />
      </div>
      </div>
    </div>


</template>

<script>
  import contentModelOne from '../ContentModel/ContentModelOne.vue'
  import contentModelTwo from '../ContentModel/ContentModelTwo.vue'
  import contentModelThree from '../ContentModel/ContentModelThree.vue'

  import inputContentModelOne from '../InputBlock/InputContentModelOne.vue'
  import inputContentModelTwo from '../InputBlock/InputContentModelTwo.vue'
  import inputContentModelThree from '../InputBlock/InputContentModelThree.vue'

  export default{
    name:'contentOrInput',
    props:['model','tittle1','tittle2','tittle3','content','index','change','del','edit','edited','inputIndex'],
    components:{
      contentModelOne,contentModelTwo,contentModelThree,
      inputContentModelOne,inputContentModelTwo,inputContentModelThree
    },
    data(){
       return{
         isContentOne:this.model==='contentModelOne',
         isContentTwo:this.model==='contentModelTwo',
         isContentThree:this.model==='contentModelThree',
       }
    },
    computed:{

    },
    mounted(){
    }
  }
</script>

