<template>
    <div id="rightPane" class="col-sm-9 col-xs-9 col-md-9 right-block">
        <header-info
          v-if="show[0]"
          className="head-name-block info-block"
        />
        <education-block
          v-if="show[1]"
          className=" info-block tittle-part"
          :del_="del(1)"
        />
        <project-experience
          v-if="show[2]"
          className=" info-block tittle-part"
          :del_="del(2)"
        />
        <self-assessment
          v-if="show[3]"
          className=" info-block tittle-part"
          :del_="del(3)"
        />
        <school-experience
          v-if="show[4]"
          className=" info-block tittle-part"
          :del_="del(4)"
        />
        <out-experience
          v-if="show[5]"
          className=" info-block tittle-part"
          :del_="del(5)"
        />
        <work-experience
          v-if="show[6]"
          className="info-block tittle-part"
          :del_="del(6)"
        />
        <skill-certificate
          v-if="show[7]"
          className=" info-block tittle-part"
          :del_="del(7)"
        />
        <price-block
          v-if="show[8]"
          className=" info-block tittle-part"
          :del_="del(8)"
        />
    </div>
</template>

<script>
    import headerInfo from './HeaderInfo.vue'
    import educationBlock from './EducationBlock.vue'
    import skillCertificate from './SkillCertificate.vue'
    import schoolExperience from './SchoolExperience.vue'
    import outExperience from './OutExperience.vue'
    import workExperience from './WorkExperience.vue'
    import projectExperience from './ProjectExperience.vue'
    import priceBlock from './PriceBlock.vue'
    import selfAssessment from './SelfAssessment.vue'


    export default{
      name: 'RightIntro',
      components: {
        headerInfo,
        educationBlock,
        skillCertificate,
        schoolExperience,
        outExperience,
        workExperience,
        projectExperience,
        priceBlock,
        selfAssessment
      },
      data(){
        return {
          show: [true, true, true, true, true, true, true, true, true]
        }
      },
      methods: {
        del: function (index) {
          var self = this;
          return function () {
            if (confirm('确定删除次模块？删除不可恢复！刷新界面才可恢复！')) {
              self.$set(self.show,index, false);
            }
          }
        }
      }
    }
</script>

<style>
    .right-block{
        width: 100%;
        background-color: white;
    }
    .info-block{
      padding:1rem calc(2rem - 1px );
      font-family: 微软雅黑;
      margin: 2rem 0;
      position: relative;
      box-sizing: border-box;

    }
    .info-block h1{
        font-size: 5rem;
        padding: 0 0;
        margin: 0 0;
        color: rgb(37,70,101);
        line-height: 6.5rem;
        text-align: start;
    }
    .info-block h2{
        font-size: 3rem;
        padding: 0 0;
        margin: 0 0;
        color: rgb(37,70,101);
        line-height: 4rem;
        text-align: start;
    }
    .info-block h3{
        font-size: 2rem;
        padding: 0 0;
        margin: 0 0;
        color: rgb(37,70,101);
        line-height: 2.5rem;
        text-align: start;
    }
    .info-block h3.wanted{
        font-weight: 400;
    }

    .info-block .menuBtn{
      position: absolute;
      height: 2.5rem;
      width: 5rem;
      margin-right: 0.5rem;
      margin-top: 0.5rem;
      top: 0;
      right: 0;
    }
    .info-block .menuBtn a{
      display: block;
      float: left;
      color: rgb(37,70,101);
    }
    .info-block .menuBtn a:hover{
      text-decoration: none;
    }
    .info-block .menuBtn a:hover section{
      cursor: pointer;
      color: red;
      transform: rotateZ(180deg) scale(1.5);

    }

    .info-block .menuBtn a section{
      display: block;
      height: 2.5rem;
      width: 2.5rem;
      font-size: 2.5rem;
      line-height: 2.5rem;
      text-align: center;
      margin-left: auto;
      margin-right: auto;
      transition: all 0.3s linear;

    }

    .head-name-block{
        margin: 2rem 0;
        text-align: start;
    }

    .tittle-part header{
        width: 100%;
        color: rgb(37,70,101);
        display: block;
        height: 5rem;
    }
    .tittle-part header>span{
        float: left;
        line-height: 4rem;
    }
    .tittle-part header>span:nth-child(1){
        width: 4rem;
        font-size: 2.5rem;
        height: 4rem;
        text-align: center;
        border-radius: 8rem;
        background-color: rgb(37,70,101);
        color: #fff;
    }
    .tittle-part header>span:nth-child(2){
        width: calc(100% - 6rem);
        margin-left: 0.5rem;
        height: 4rem;
        vertical-align: top;
        border-bottom: 2px solid rgb(37,70,101);
    }

    .intro-content-1 li{
        /*background-color: #00AA88;*/
        padding: 1rem 0;
        text-align: start;
        word-break: break-all;
    }
    .intro-content-1 li section p{
        letter-spacing: 0.2rem;
        line-height: 2rem;
        margin: 0 0;
    }
    .intro-content-1 li section:nth-child(1) span{
        float: left;
        width: 33%;
        margin: 0 0;
        font-size: 2rem;
    }
    .intro-content-1 li section:nth-child(1) span:nth-child(1){
      width: 32% ;
      margin-left: 1%;
    }
    .intro-content-1 li section:nth-child(1) span:nth-child(3) p{
      padding-right: 4rem;
    }
    .intro-content-1 li section:nth-child(2){
        margin: 1% 1%;
        width: 98%;
        padding-top: 0.5rem;
        font-size: 1.8rem;
    }

    .intro-content-1 li section input:focus,
    .intro-content-1 li section textarea:focus,
    .intro-content-2 li section textarea:focus,
    .intro-content-3 section textarea:focus
    {
      outline:none;
    }

    .intro-content-2{
        padding: 0 3rem;
        list-style: disc;
    }
    .intro-content-2 li{
        /*background-color: #00AA88;*/
        padding: 1rem 0;
        text-align: start;
        word-break: break-all;
    }
    .intro-content-2 li h4{
        font-size: 2rem;
        margin: 0 0;
    }



    .intro-content-3{
        padding: 1rem 2rem  3.5rem 1rem;
        text-align: start;
        word-break: break-all;
        font-size: 1.8rem;
    }
    .intro-content-3 p{
        letter-spacing: 0.2rem;
        line-height: 2rem;
        margin: 0 0;
    }
    .hoverBlock{
      border: 1px dashed rgb(37,70,101);
    }

</style>



