<template>
  <div :class="[className,{hoverBlock:hover}]" @mouseover="isHover" @mouseleave="isNotHover">

  <header class="clear-fix">
            <span class="fa fa-star"></span>
            <span>
                <h2>荣誉奖项</h2>
            </span>
        </header>
        <ul class="intro-content-2">
          <content-o-r-input
            v-for="(content,index) in contents"
            :model="'contentModelTwo'"
            :content="content"
            :index="index"
            :key="index"
            :change="changeData(index)"
            :edit="edit(index)"
            :edited="edited(index)"
            :del="del(index)"
            :inputIndex="inputIndex"
          />
        </ul>
    <section class="menuBtn" v-show="hover">
      <a title="删除此版块" @click="del_">
        <section class="fa fa-minus-circle"></section>
      </a>
      <a title="增加一条" @click="add">
        <section class="fa fa-plus-circle"></section>
      </a>
    </section>
    </div>
</template>

<script>
  import contentORInput from './ContentORInput/ContentORInput.vue'

    export default{
      name:'priceBlock',
      props:['className','del_'],
      components:{
        contentORInput
      },
      data(){
        return{
          contents:[
            'XXXX奖',
            'XXXX奖'

          ],
          hover:false,
          inputIndex:-1
        }
      },
      methods:{
        isHover:function(e){
          this.hover=true
        },
        isNotHover:function(e){
          this.hover=false
        },
        changeData:function(index){
          var self=this;
          return function(value){
            self.contents[index]=value;
          }
        },
        edit:function(index){
          var self=this;
          return function(){
            self.inputIndex=index;
          }
        },
        edited:function(){
          var self=this;
          return function(){
            self.inputIndex=-1;
          }
        },
        del:function(index){
          var self=this;
          return function(){
            self.inputIndex=-1;
            self.contents.splice(index,1)
          }
        },
        add:function(){
          this.contents.length<20&&this.contents.push('XXXXXX')
        }
      }

    }
</script>

<style></style>



