<template>
    <div :class="className">
        <div class="upload-btn" @click="toggleShow" @mouseover="toggleNotice('over')" @mouseout="toggleNotice('out')">
            <img :src="imgDataUrl" class="img-thumbnail">
            <div class="upload-notice" v-show="notice">
                <span>点击更换头像</span>
            </div>
        </div>
        <my-upload field="img"
                   @crop-success="cropSuccess"
                   @crop-upload-success="cropUploadSuccess"
                   @crop-upload-fail="cropUploadFail"
                   v-model="show"
                   :width="180"
                   :height="250"
                   url="/upload"
                   img-format="png"></my-upload>
    </div>
</template>

<script>
import logo from '../../assets/images/h.jpg'
import myUpload from 'vue-image-crop-upload/upload-2.vue';
export default {

    name: 'headImg',
    props:['className'],
    components: {
      myUpload
    },
    data () {
        return {
          show: false,
          imgDataUrl: logo,
          notice: false
    }
    },
  methods: {
    toggleShow() {
      this.show = !this.show;
    },
    cropSuccess(imgDataUrl, field){
      console.log('-------- crop success --------');
      this.imgDataUrl = imgDataUrl;
    },
    cropUploadSuccess(jsonData, field){
      console.log('-------- upload success --------');
      console.log(jsonData);
      console.log('field: ' + field);
    },
    cropUploadFail(status, field){
      console.log('-------- upload fail --------');
      console.log(status);
      console.log('field: ' + field);
    },
    toggleNotice(type) {
      switch (type) {
        case 'over': this.notice = true;break;
        case 'out': this.notice = false;break
      }
    }
  }
}
</script>

<style>
    .upload-btn {
        position: relative;
        width: 100%;
        height: 100%;
        cursor: pointer;
    }
    .upload-notice {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: rgba(0,0,0,.3);
        padding-top: 16rem;
    }
    .upload-notice span{
        color: #7d7d7d;
        font-weight: bold;
        font-size: 1.5rem;
        text-shadow: 1px 1px #000;
    }
    .vicp-drop-area,.vicp-upload {
        height: 20rem!important;
    }
</style>



