<template>
    <div>
      <button @click="change">hello word</button>
      <div v-for="(c,i) in a">
        <div
          :is="c"
          :key="i"
        />
      </div>
    </div>
</template>

<script>
  import Vue from 'vue'

  var hello=Vue.component('hello',{
      template:`<div style="color:white">{{hello}}</div>`,
    data(){
          return{
              hello:'hello',
            timeId:null
          }
    }
  })
  var world=Vue.component('world',{
      template:`<div style="color:white">{{hello}}</div>`,
    data(){
          return{
              hello:'world',
            timeId:null
          }
    }
  })

 export default{
   name:'test',
   data(){
       return{
          a:[hello,hello,hello]
       }
   },
   components:{
       hello,world
   },
   methods:{
       change(){
         var self=this;
         self.a[0]=world;
//           self.$set(self.a,0,world);
       }
   }
   ,
   mounted(){

   },
   updated(){

   }
   ,
   destroyed(){

   }
 }
</script>

<style>

</style>



