<template>
    <div class="icon-collection">
        <div class="icon-bar">
            选择一个图标
        </div>
        <ul class="clear-fix">
            <li class="tiny-icon" v-for="item in icons" @click="selectIcon(item)">
                <i class="fa" :class="item"></i>
            </li>
        </ul>
    </div>
</template>

<script type="text/ecmascript-6">
import { icons } from '../../assets/js/icons.js';
export default {
  name: 'icon-block',
  data() {
    return {
      icons: icons,
      mask: true
    }
  },
  mounted() {

  },
  methods: {
    hideMask() {
        this.mask = false;
    },
    selectIcon(item) {
      this.$emit('changeIcon',item);
    }
  }
}
</script>

<style>
    .icon-collection {
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 60rem;
        height: 40rem;
        z-index: 2000;
        color: #000!important;
        background: #fff;
        border-radius: 2rem;
    }
    .icon-bar {
        width: 100%;
        height: 6rem;
        line-height: 6rem;
        font-size: 3rem;
        font-family: 微软雅黑;
        text-align: center;
        vertical-align: middle;
        color: #fff;
        background-color: #254665;
        border-bottom: 2px solid #e5e5e5;
        border-radius: 2rem 2rem 0 0;
    }
    .icon-collection ul {
        padding: 0 0 1rem 2rem;
        height: 34rem;
        overflow-y: auto;
        text-align: center;
    }
    .icon-collection ul>li{
        display: inline-block;
        width: 4rem;
        height: 4rem;
        float: left;
    }
    .tiny-icon:hover {
        background-color: #2b669a;
    }
    .tiny-icon .fa{
        width: 4rem;
        line-height: 4rem;
        color: #000;
        font-size: 3rem;
        cursor: pointer;
        text-align: center;
        vertical-align: middle;
    }
    .tiny-icon .fa:hover {
        color: #fff;
    }
</style>


